.detail {
  background-color: #e4e7eb;
  padding-right: 0px;
  padding-left: 0px;
}

.expensesListe {
  max-width: 300px;
  overflow-x: scroll;
}

.detail .ticket ul {
  padding: 0;
  list-style: none;
  margin-left: -15px;
  margin-right: -15px;
}

.detail .ticket ul li {
  padding: 4px;
}

.detail .ticket .total {
  font-weight: 500;
}

.table-total {
  margin-top: 10px;
}

i.category {
  width: 20px;
}

.detailForm {
  border-top: 1px solid #cbd2d9;
  margin-top: 15px;
  padding-top: 15px;
}

.nav-stacked > li.tab-detail + li.tab-detail {
  margin-top: 0px;
  margin-left: 0;
}

.nav-pills > li.tab-detail.active > a,
.nav-pills > li.tab-detail > a:hover {
  border-radius: 0px;
  border-left: 3px solid #323f4b;
  color: #323f4b;
}

.nav-pills > li.active.tab-detail > a,
.nav-pills > li.active.tab-detail > a:focus,
.nav-pills > li.active.tab-detail > a:hover,
.nav > li.tab-detail > a:focus,
.nav > li.tab-detail > a:hover,
.nav-pills > li.tab-detail > a {
  border-radius: 0px;
  color: #323f4b;
  background-color: transparent;
}
.toDoListBL li,
.myLuggage li,
.forecasts li {
  cursor: pointer;
}
.toDoListBL li.done,
.myLuggage li.done,
.forecasts li.done {
  text-decoration: line-through;
  color: #9aa5b1;
}
